@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.rust-servers {
  margin-top: 7%;
  margin-left: 22%;
  width: 160px;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  border-bottom: 1px solid aquamarine;
}
@media only screen and (max-width: 420px){
  h1{
    font-size: 15px;
    width: 120px;
  }
  .rust-servers {
  font-size: 15px;
  width: 120px;
}
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Montserrat;
    font-size: 20px;
    background-color: #1a1a1c;
    color: #fff;
    
  }


  .players_online{
      color:aquamarine;
      margin:5px;
      width:115px;
  }

  
  .server {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    max-width: 100%;
  }

  .server_name {
    margin-left:40px;
    text-align: start;
    width: 320px;
  } 
  
  .wipe{
    text-align: right;
    justify-content: space-between;
    padding-left: 30px;
    width: 350px;
    font-size: 17px;
  }
  
  @media only screen and (max-width: 420px){

    .server_name h1{
      font-size: 12px;
      width: 160px;
    }
    .players_online{
      font-size: 15px;
      
    }
    .wipe{
      font-size: 10px;
      width: 120px;
      padding-right: 30px;
    }
  }
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat;
  font-size: 20px;
  background-color: #1a1a1c;
  color: #fff;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-top:100px;
  margin-left:30%;
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 150px;
  height: 25px;
  border-radius: 4px;
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

