
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Montserrat;
    font-size: 20px;
    background-color: #1a1a1c;
    color: #fff;
    
  }


  .players_online{
      color:aquamarine;
      margin:5px;
      width:115px;
  }

  
  .server {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    max-width: 100%;
  }

  .server_name {
    margin-left:40px;
    text-align: start;
    width: 320px;
  } 
  
  .wipe{
    text-align: right;
    justify-content: space-between;
    padding-left: 30px;
    width: 350px;
    font-size: 17px;
  }
  
  @media only screen and (max-width: 420px){

    .server_name h1{
      font-size: 12px;
      width: 160px;
    }
    .players_online{
      font-size: 15px;
      
    }
    .wipe{
      font-size: 10px;
      width: 120px;
      padding-right: 30px;
    }
  }