@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat;
  font-size: 20px;
  background-color: #1a1a1c;
  color: #fff;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-top:100px;
  margin-left:30%;
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 150px;
  height: 25px;
  border-radius: 4px;
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}
